import React, {Component} from 'react'
import Caption from './caption'
import Textarea from './textarea'
import domtoimage from 'dom-to-image'
import Popup from "reactjs-popup"
import {TelegramShareButton, TelegramIcon} from 'react-share'

class MemeGenerator extends Component {
    state = {
        memeImage: this.props.image,
        captionCount: 0,
        captionsArray: [],
        yourMeme: undefined,
        url: null
    }

    addCaption = () => {
        this.setState(prevState => {
            const updatedArray = prevState.captionsArray
            updatedArray.push({
                id: (prevState.captionsArray.length + 1).toString(),
                text: 'sample text',
                fontSize: '6'
            })
            return ({captionCount: updatedArray})
        })
    }

    handleChange = (e) => {
        const {name, value, id} = e.target

        const updatedArray = this.state.captionsArray.map(item => {
            if (item.id === id) {
                item[name] = value
            }
            return item
        })

        this.setState({
            captionsArray: updatedArray
        })
    }

    resetImage = () => {
        this.props.onResetImage(null)
    }

    saveImage = (event) => {
        event.preventDefault()
        domtoimage.toJpeg(document.getElementById('my-node'), {quality: 0.95})
        //     .then(function (dataUrl) {
        //         // var link = document.createElement('a');
        //         // link.download = 'my-image-name.jpeg';
        //         // link.href = dataUrl;
        //         // // link.click()
        //         // console.log(dataUrl)
        //         // return link.href
        //
        //         // return dataUrl
        //
        //
        //         const image = new Image()
        //         image.src = dataUrl
        //         const canvas = document.getElementById('my-node')
        //         const uri = canvas.toDataURL()
        //         console.log(uri)
        //         // console.log(image.src)
        //         // document.body.appendChild(image)
        //         // return image
        //     })
            .then(dataUrl => this.setState({yourMeme: dataUrl})
            ).catch()

        // this.setState({
        //     yourMeme: image
        // })
        // console.log(image)


        const b64Data = this.state.yourMeme
        if (b64Data) {
            const url = URL.createObjectURL(new Blob([b64Data] , {type:'text/plain'}));
            this.setState({
                url: url
            })
        }
    }

    render() {
        const yourMeme = this.state.yourMeme
        const url = this.state.url
        // console.log(yourMeme)
        return (
            <div className="memeGenerator">
                <div className="controls">
                    <button onClick={this.resetImage}>Another image</button>
                    <button onClick={this.addCaption}>Add Caption</button>
                    <button onClick={this.saveImage}>Save meme</button>
                </div>
                <div id="my-node" className="memeContainer">
                    <img src={this.state.memeImage} alt="meme_image"/>
                    {this.captions}
                </div>
                {(this.state.captionsArray.length) ? (
                    <div>
                        <h2 className="mt-1 text-center">Edit captions text and size</h2>
                        {this.inputs}
                    </div>
                ) : ''}
                {url &&
                <TelegramShareButton url={url}>
                    <TelegramIcon />
                </TelegramShareButton>}
            </div>
        )
    }

    get captions() {
        return this.state.captionsArray.map(item => {
            return (
                <Caption key={item.id} data={item} />
            )
        })
    }

    get inputs() {
        return this.state.captionsArray.map(item => {
            return (
                <div className="captionControl" key={item.id}>
                    <Textarea
                        onChange={(evt) => this.handleChange(evt)}
                        name="text"
                        id={item.id}
                        value={item.text}
                    />
                    <input
                        name="fontSize"
                        id={item.id}
                        onChange={this.handleChange}
                        type="number"
                        value={item.fontSize}
                    />
                </div>
            )
        })
    }
}

export default MemeGenerator
